<script setup lang="ts">
import { ref, onMounted } from 'vue'
import InputText from 'primevue/inputtext'
import { useRouter } from 'vue-router'
import Button from 'primevue/button'
const router = useRouter()
import { bind } from 'wanakana'

const searchFor = ref('')

onMounted(() => {
  bind(txtSearch.value.$el)
})

const go = () => router.push('/Dictionary/' + searchFor.value)

const txtSearch = ref()
</script>

<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <InputText
          ref="txtSearch"
          placeholder="Search"
          type="text"
          v-model="searchFor"
          @keyup.enter="go"
        />
        <Button @click="go">Search</Button>
      </div>
    </div>
  </div>
  <RouterView />
</template>

<style scoped lang="scss"></style>
